.flow-timer-div {
  position: relative;
  margin: 1rem;
  padding-bottom: calc(2em + 60px);
  background: white;
  box-shadow: 2px 4px 25px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  overflow: hidden;
  transition: all 0.3s linear;
  border: 3px solid magenta;

  .clock-display {
    font-weight: 900;
    font-size: 3em;
  }
}
